@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@700&family=Sacramento&display=swap');

/* 
  配色はこちらを参照
  https://colordrop.io/color/17935
 */
$color-base:#2694ab;
$color-second: #e59572;
$color-hover: #e5cabe;
$color-accent: #ea7070;
$color-font:#f6f6e9;
$color-main:#f6f6e9;
$color-side:#f6f6e9;
$color-footer: #e59572;

// $pc: 1024px; // PC
$tab: 799px; // タブレット
// $sp: 480px;  // スマホ

// @mixin pc {
//   @media (max-width: ($pc)) {
//     @content;
//   }
// }
@mixin tab {
  @media (max-width: ($tab)) {
    @content;
  }
}
