body{
  margin: 0;
  background-color: $color-base;

  header {
    background-color: $color-second;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    .title {
      width: 100%;
      height: 200px;
      color: $color-font;
      font-family: 'Noto Serif', serif;
      font-size: 3em;
      text-align: center;
      line-height: 200px;

      @include tab {
        height: 100px;
        font-size: 2em;
        text-align: center;
        line-height: 100px;
      };
    }
  } /* header */


  main {
    width: 1000px;
    min-height: 1000px;
    margin: 20px auto;
    @include tab {
      width: 100%;
    };
    .contanor {
      width: 100%;
      display: flex;
      height: auto;
      @include tab {
      };
      article {
        background-color: $color-main;
        width: 74%;
        height: auto;
        .content {
          margin: 10px;
          border: 1px $color-accent solid;
          .top {
            position: relative;
            padding: 4px;
            margin-bottom: 40px;
            .title {
              margin-left: 100px;
            }
            .category {
              margin-left: 100px;
            }
            .date {
              color: $color-font;
              background:$color-accent;
              border-radius: 50%;
              width: 80px;
              height: 80px;
              font-size: 1rem;
              text-align: center;
              position: absolute;
              top: 0;
              padding-top: 10px;
              span {
                font-size: 0.8rem;
                border-top: 1px $color-font solid;
                padding-top: 6px;
                display: block;
                width: 60%;
                margin: 0 auto;
              }
            }
            .blogbody {
              margin-left: 100px;
              display: flex;
              img {
                width: 340px;
                height: auto;
              }
              .sentence {
                margin-left: 20px;
                margin-right: 10px;
              }
            }
          }
        }
      }
      aside {
        margin-left: 10px;
        background-color: $color-side;
        width: 26%;
        height: auto;
        .content {
          margin: 10px;
          .title {
            font-size: 1.1rem;
            border-bottom: 2px $color-accent solid;
          }
        }
        ul.recent {
          margin-left: -20px;
          li {
            font-size: 0.7rem;
            list-style: none;
            margin-bottom: 10px;
            a {
              color: $color-accent;
              text-decoration: none;
            }
            a:hover {
              background-color: $color-hover;
            }
          }
        }
        ul.category {
          margin-left: -20px;
          li {
            font-size: 0.7rem;
            list-style: none;
            margin-bottom: 10px;
            display: inline-block;
            background-color: $color-accent;
            padding: 5px;
            border-radius: 5%;
            box-sizing: border-box;
            cursor: pointer;
            a {
              top: 0;
              left: 0;
              text-decoration: none;
              color: $color-font;
            }
          }
          li:hover {
            background-color: $color-second;
          }
        }
      }
    }
  } /* main */


  footer {
    width: 100%;
    background-color: $color-second;
    position: absolute;/*←絶対位置*/
    // bottom: 0; /*下に固定*/
    height: 50px;
    // .contanor {
    // };

  } /* footer */


}



